
.vuejs-heatmap text.month-name,
.vuejs-heatmap text.calendar-heatmap-legend-text,
.vuejs-heatmap text.day-initial {
  font-size: 10px;
  fill: inherit;
  font-family:  -apple-system, BlinkMacSystemFont, Helvetica, arial, 'Open Sans', sans-serif;
}
.vuejs-heatmap rect.day-cell:hover {
  stroke: #555555;
  stroke-width: 1px;
}
.vuejs-heatmap .day-cell-tooltip {
  position: absolute;
  z-index: 9999;
  padding: 5px 9px;
  color: #bbbbbb !important;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.85) !important;
  border-radius: 3px;
  text-align: center;
}
.vuejs-heatmap .day-cell-tooltip > span {
  color: white !important;
  font-family:  -apple-system, BlinkMacSystemFont, Helvetica, arial, 'Open Sans', sans-serif
}
.vuejs-heatmap .calendar-heatmap {
  box-sizing: initial;
}



